.container {
  text-align: center;
  align-items: center;
  display: flex;
  flex-direction: column;

}
.title {
  font-size: 40px;
  color: #fff;
  margin-top: 50px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.title span {
  color: #26ffcb;
  padding-left: 15px;
  text-transform: capitalize;
}

.reset {
  width: 200px;
  height: 60px;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 50px;
  background: #1f3540;
  font-size: 26px;
  color: #26ffcb;
  margin-top: 25px;
  margin-bottom: 50px;
}

.boxes {
  display: flex;
  height: 80px;
  width: 80px;
  background: #1f3540;
  border: 4px solid #0f1b21;
  border-radius: 12px;
  cursor: pointer;
}
.board{
  height: 300px;
  width: 564px;
  display: flex;
  align-items: center;
  justify-content: center;

}

.boxes img{
  margin: 20px;
}

.title img{
  height: 50px;
  padding: 0 10px;
}


/* RESONSIVE DESIGN */
@media (max-width:650px) {
  .reset {
    width: 150px;
    height: 50px;
  }

  .title {
    font-size: 30px;
  }
}

@media (max-width:450px) {
  .title {
    font-size: 25px;
  }
}